.b-hero {
	display: flex;
	overflow: hidden;
	height: 100vh;

	&__item {
		width: 50%;

		&_content {
			position: relative;
			z-index: 1; // to be over &_img

			display: flex;
			align-items: center;
			justify-content: center;

			min-width: 710px;

			padding: 100px 40px;

			box-shadow: 0px 10px 60px 0 rgba( 10, 26, 51, .2 );

			@media only screen and ( max-width: 1640px ) {
				padding: 100px 30px; }

			@media only screen and ( max-width: 1500px ) {
				min-width: 600px; } }

		&_img {
			position: relative; // for &::before and &__img
			overflow: hidden; // for &__img

			background: {
				image: url( '#{$img}/b-hero/bg_decoration.png' );
				position: -64px -15px;
				size: 291px 341px;
				repeat: no-repeat; }

			@include md-retina {
				background-image: url( '#{$img}/b-hero/bg_decoration@2x.png' ); }

			&::before {
				content: '';

				position: absolute;
				bottom: 4vh;
				left: calc( 3.6vw - 1.1111111vh );

				width: 3.6666667vh;
				height: 50.5555556vh;

				background: {
					image: url( '#{$img}/b-hero/bg_decoration_2.png' );
					position: center;
					size: cover;
					repeat: no-repeat; } } } }

	&__img {
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		background: url( '#{$img}/b-hero/bg_2.jpg' ) center / cover no-repeat;

		transform: scale( 1 );

		transition: transform .2s;

		&:hover {
			transform: scale( 1.05 ); } }

	&__content {
		position: relative; // for &__btn
		max-width: 600px; }

	&__heading {
		position: relative;

		display: flex; // to collapse margins inside
		flex-direction: column;

		margin-bottom: 60px;
		border-bottom: 1px solid $black;
		padding-bottom: 35px;

		&::before,
		&::after {
			content: '';

			position: absolute;
			bottom: -5px;

			width: 9px;
			height: 9px;

			background-color: $black; }

		&::before {
			left: 0; }

		&::after {
			right: 0; } }

	&__heading-text {
		margin: -13px 0 22px;

		color: $black;
		font-size: 50px;
		line-height: 60px;
		font-weight: 700;
		letter-spacing: .018em;

		@media only screen and ( max-width: 1500px ) {
			margin: -12px 0 16px;
			font-size: 42px;
			line-height: 56px; }

		&_highlighted {
			color: $brand-primary; }

		&_last {
			margin-bottom: -13px; } }

	&_theme_1 .b-hero__heading-text_highlighted {
		color: #ccad52; }

	&_theme_2 .b-hero__heading-text_highlighted {
		color: #99b9bf; }

	&_theme_5 .b-hero__heading-text_highlighted {
		color: #ff3030; }

	&_theme_6 .b-hero__heading-text_highlighted {
		color: #74a6b3; }

	&_theme_8 .b-hero__heading-text_highlighted {
		color: #d4b128; }

	&_theme_9 .b-hero__heading-text_highlighted {
		color: #f2b90c; }

	&__subheading-text {
		margin: -9px 0 -11px;

		color: $black;
		font-size: 16px;
		line-height: 2;
		font-weight: 600;
		font-style: italic;
		letter-spacing: .04em; }

	&__list {
		counter-reset: list;

		display: flex;
		flex-wrap: wrap;
		align-items: flex-start;

		margin: -15px; }

	&__list-item {
		position: relative; // for &::before

		display: flex;
		align-items: center;

		max-width: 235px;
		min-height: 150px;

		margin: 15px;
		padding: 35px;

		background-color: #f5f5f5;

		&::before {
			content: counter( list ) '.';
			counter-increment: list;

			position: absolute;
			top: 10px;
			right: calc( 100% - 10px );

			color: $brand-primary;
			font-size: 24px;
			line-height: 1;
			font-weight: 900;
			letter-spacing: .08em; }

		&:nth-child( even ) {
			margin-top: 100px;
			margin-bottom: -120px; } }

	&_style_2 .b-hero__list-item {
		width: calc( 50% - 30px );
		max-width: none;
		min-height: 170px; }

	&_theme_1 .b-hero__list-item::before {
		color: #ccad52; }

	&_theme_2 .b-hero__list-item::before {
		color: #99b9bf; }

	&_theme_5 .b-hero__list-item::before {
		color: #ff3030; }

	&_theme_6 .b-hero__list-item::before {
		color: #ccad52; }

	&_theme_8 .b-hero__list-item::before {
		color: #d4b128; }

	&_theme_9 .b-hero__list-item::before {
		color: #f2b90c; }

	&__list-text {
		margin: -7px 0 -9px;

		color: $black;
		font-size: 13px;
		line-height: 26px;
		font-weight: 600;
		letter-spacing: .06em; }

	&_style_2 .b-hero__list-text {
		margin: -9px 0 -10px;

		font-size: 15px;
		line-height: 30px;
		font-weight: 500;

		&_highlighted {
			font-weight: 800; } }

	&__btn { // tag a
		position: absolute;
		bottom: 0;
		left: 265px;

		display: block;

		&::before {
			content: '';

			position: absolute;
			bottom: 0;
			right: -17px;

			opacity: .2;

			width: 100%;
			height: calc( 100% + 17px );

			background: $brand-primary;

			transition: all .2s; }

		&:hover::before {
			bottom: 5px;
			right: -22px; }

		&:active::before {
			bottom: 0;
			right: 0;
			height: 100%; }

		&::after {
			content: '';

			position: absolute;
			top: calc( 100% + 10px );
			left: 17px;

			width: calc( 100% - 17px );
			height: 1px;

			background: $brand-primary;

			transition: all .2s; }

		&:hover::after {
			width: 120px; }

		&:active::after {
			width: 20px; } }

	&_style_2 .b-hero__btn {
		left: calc( 50% + 15px );
		width: calc( 50% - 15px );

		&::after {
			width: calc( 100% - ( 17px + 50px ) ); } }

	&_theme_1 .b-hero__btn::before,
	&_theme_1 .b-hero__btn::after {
		background: #333e50; }

	&_theme_5 .b-hero__btn::before,
	&_theme_5 .b-hero__btn::after {
		background: #ff3030; }

	&_theme_6 .b-hero__btn::before,
	&_theme_6 .b-hero__btn::after {
		background: #74a6b3; }

	&_theme_8 .b-hero__btn::before,
	&_theme_8 .b-hero__btn::after {
		background: #d4b128; }

	&_theme_9 .b-hero__btn::before,
	&_theme_9 .b-hero__btn::after {
		background: #f2b90c; }

	&__btn-inner { // tag span
		position: relative;
		display: block;

		&::before {
			content: '';

			position: absolute;
			top: -11px;
			left: -11px;

			width: 20px;
			height: 1px;

			background: $brand-primary;

			transition: all .2s; }

		&::after {
			content: '';

			position: absolute;
			top: -11px;
			left: -11px;

			width: 1px;
			height: 20px;

			background: $brand-primary;

			transition: all .2s; } }

	&_theme_1 .b-hero__btn-inner::before,
	&_theme_1 .b-hero__btn-inner::after {
		background: #333e50; }

	&_theme_5 .b-hero__btn-inner::before,
	&_theme_5 .b-hero__btn-inner::after {
		background: #ff3030; }

	&_theme_6 .b-hero__btn-inner::before,
	&_theme_6 .b-hero__btn-inner::after {
		background: #74a6b3; }

	&_theme_8 .b-hero__btn-inner::before,
	&_theme_8 .b-hero__btn-inner::after {
		background: #d4b128; }

	&_theme_9 .b-hero__btn-inner::before,
	&_theme_9 .b-hero__btn-inner::after {
		background: #f2b90c; }

	&__btn:hover .b-hero__btn-inner::before {
		left: -21px; }

	&__btn:active .b-hero__btn-inner::before {
		left: -25px; }

	&__btn:hover .b-hero__btn-inner::after {
		top: -21px; }

	&__btn:active .b-hero__btn-inner::after {
		top: -25px; }

	&__btn-inner-2 { // tag span
		position: relative;
		z-index: 1; // to be over .b-hero__btn::before, .b-hero__btn::after and .b-hero__btn-inner::before, .b-hero__btn::before
		display: flex;
		justify-content: center;

		width: 235px;

		padding: 19px 25px;

		background: $brand-primary;
		box-shadow: 0px 20px 60px 0 rgba( $brand-primary, .3 );

		transition: all .2s; }

	&_style_2 .b-hero__btn-inner-2 {
		width: 100%; }

	&__btn:hover .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( $brand-primary, .5 ); }

	&__btn:active .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( $brand-primary, 0 ); }

	&_theme_1 .b-hero__btn-inner-2 {
		background: #333e50;
		box-shadow: 0px 20px 60px 0 rgba( #142033, .3 ); }

	&_theme_5 .b-hero__btn-inner-2 {
		background: #ff3030;
		box-shadow: 0px 20px 60px 0 rgba( #800d0d, .3 ); }

	&_theme_6 .b-hero__btn-inner-2 {
		background: #74a6b3;
		box-shadow: 0px 20px 60px 0 rgba( #396773, .3 ); }

	&_theme_8 .b-hero__btn-inner-2 {
		background: #d4b128;
		box-shadow: 0px 20px 60px 0 rgba( #b3a46b, .3 ); }

	&_theme_9 .b-hero__btn-inner-2 {
		background: #f2b90c;
		box-shadow: 0px 20px 60px 0 rgba( #bfaf7c, .3 ); }

	&_theme_1 .b-hero__btn:hover .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #142033, .5 ); }

	&_theme_5 .b-hero__btn:hover .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #800d0d, .5 ); }

	&_theme_6 .b-hero__btn:hover .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #396773, .5 ); }

	&_theme_8 .b-hero__btn:hover .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #b3a46b, .5 ); }

	&_theme_9 .b-hero__btn:hover .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #bfaf7c, .5 ); }

	&_theme_1 .b-hero__btn:active .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #142033, 0 ); }

	&_theme_5 .b-hero__btn:active .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #800d0d, 0 ); }

	&_theme_6 .b-hero__btn:active .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #396773, 0 ); }

	&_theme_8 .b-hero__btn:active .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #b3a46b, 0 ); }

	&_theme_9 .b-hero__btn:active .b-hero__btn-inner-2 {
		box-shadow: 0px 20px 60px 0 rgba( #bfaf7c, 0 ); }

	&__btn-text { // tag span
		position: relative; // for &:: after
		left: 0;

		display: block;

		padding-right: 55px;

		color: $white;
		font-size: 14px;
		line-height: 24px;
		font-weight: 700;
		letter-spacing: .06em;

		text-align: center;
		text-transform: uppercase;

		transition: all .2s;

		@extend .i-arrow-3;

		&::after {
			position: absolute;
			top: 6px;
			right: 0;

			color: $white;
			font-size: 13px; } }

	&__btn:hover .b-hero__btn-text {
		left: 4px; }

	&__btn:active .b-hero__btn-text {
		left: 8px; }

	&__scroll {
		position: absolute;
		bottom: 0;
		left: 50%;

		padding: 18px 60px 18px 0;

		transform: translateX( -50% );

		@extend .i-arrow-2;

		&::before {
			content: '';

			position: absolute;
			bottom: 0;
			right: 0;

			width: 47px;
			height: 45px;

			background-color: #f0f0f0;

			transition: all .2s; }

		&:hover::before {
			background-color: $brand-primary; }

		&::after {
			position: absolute;
			top: 20px;
			right: 18px;

			color: $black;
			font-size: 6px;

			transition: all .2s; }

		&:hover::after {
			top: 22px;
			color: $white; } }

	&__scroll-text { // tag span
		display: block;

		margin: -2px 0;

		color: $black;
		font-size: 13px;
		line-height: 1;
		font-weight: 700;
		letter-spacing: .06em;

		.i-arrow-5 {} }

	&__demo {
		display: flex;
		align-items: center;
		justify-content: center;

		height: 100%;
		margin-left: 0; }

	&__demo-item {
		position: relative;
		padding: 20px; }

	&__demo-img {
		width: 32.3125vw;
		max-width: 517px;

		background: center / cover no-repeat;
		box-shadow: 0px 20px 100px 0 rgba( #171f26, .2 );

		&:first-child {
			transform: scale( 1 );
			transition: transform .2s; }

		&:last-child {
			position: absolute;
			top: 20px;
			left: calc( 100% + 20px ); }

		&_1_1 {
			background-image: url( '#{$img}/b-hero/1/1.jpg' ); }

		&_1_2 {
			background-image: url( '#{$img}/b-hero/1/2.jpg' ); }

		&_2_1 {
			background-image: url( '#{$img}/b-hero/2/1.jpg' ); }

		&_2_2 {
			background-image: url( '#{$img}/b-hero/2/2.jpg' ); }

		&_3_1 {
			background-image: url( '#{$img}/b-hero/3/1.jpg' ); }

		&_3_2 {
			background-image: url( '#{$img}/b-hero/3/2.jpg' ); }

		&_4_1 {
			background-image: url( '#{$img}/b-hero/4/1.jpg' ); }

		&_4_2 {
			background-image: url( '#{$img}/b-hero/4/2.jpg' ); }

		&_5_1 {
			background-image: url( '#{$img}/b-hero/5/1.jpg' ); }

		&_5_2 {
			background-image: url( '#{$img}/b-hero/5/2.jpg' ); }

		&_6_1 {
			background-image: url( '#{$img}/b-hero/6/1.jpg' ); }

		&_6_2 {
			background-image: url( '#{$img}/b-hero/6/2.jpg' ); }

		&_7_1 {
			background-image: url( '#{$img}/b-hero/7/1.jpg' ); }

		&_7_2 {
			background-image: url( '#{$img}/b-hero/7/2.jpg' ); }

		&_8_1 {
			background-image: url( '#{$img}/b-hero/8/1.jpg' ); }

		&_8_2 {
			background-image: url( '#{$img}/b-hero/8/2.jpg' ); }

		&_9_1 {
			background-image: url( '#{$img}/b-hero/9/1.jpg' ); }

		&_9_2 {
			background-image: url( '#{$img}/b-hero/9/2.jpg' ); } }

	&__item_img:hover .b-hero__demo-img {

		&:first-child {
			transform: scale( 1.05 ); } }

	&__demo-img-inner {
		padding-top: 141.1992263%; } }
